import {
  BaseConnectorConfig,
  FileActionType,
  FileRegex,
  isBaseConnectorConfigType,
} from '@xip-online-data/types';
import { SftpConfig } from '@xip-online-data/sftp-client';

export interface FileSourceConfig {
  directory: string;
  recursive?: boolean;
  fileSelector?: FileRegex;
  action: FileActionType;
  ftpIdentifier: string;
  interval: number;
  optionalHeaders?: Array<string>;
  optionalSettings?: {
    [key: string]: unknown;
  };
  indexes?: Array<string>;
  delimiter?: string;
  processedDirectory?: string;
  type?: 'metric' | 'document';
  root: string;
}

export interface FileSourceProcessConfig {
  sftpConfig: SftpConfig;
  directories: Array<FileSourceConfig>;
  forceReconnect?: boolean;
}

export function isYamlConfigType(
  obj: FileSourceProcessConfig,
): obj is FileSourceProcessConfig {
  return obj !== undefined && obj !== null;
}
